import { Component, OnInit } from '@angular/core';
import { MovieService } from 'src/app/movie.service';
import { Movie } from '../movie';
import { Location } from '@angular/common';

@Component({
  selector: 'app-movie-add',
  templateUrl: './movie-add.component.html',
  styleUrls: ['./movie-add.component.scss']
})
export class MovieAddComponent implements OnInit {

  constructor(private movieService: MovieService, private location: Location) { }

  ngOnInit(): void {
  }

  add(ranking: string, name: string, score: string, quote: string): void{
    if (!ranking || !name) { return; }
    this.movieService.addMovie({ ranking, name, score, quote } as Movie).subscribe();
  }

  goBack(): void{
    this.location.back();
  }

}
