import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { Movie } from './movie';
import { catchError, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class MovieService {

  // 云端数据库链接
  private moviesurl = 'https://106.13.193.143/movies';

  private handleError <T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  constructor(private httpClient: HttpClient) { }

  // 获取数据库中所有电影
  getMovies(): Observable<Movie[]> {
    return this.httpClient.get<Movie[]>(this.moviesurl);
  }

  // 获取指定电影详情
  getMovieById(id: string): Observable<Movie> {
    return this.httpClient.get<Movie>(this.moviesurl + '/' + id);
  }

  // 添加电影
  addMovie(movie: Movie): Observable<Movie> {
    return this.httpClient.post<Movie>(this.moviesurl, movie);
  }

  // 删除电影
  deledeMovie(id: string): Observable<any> {
    return this.httpClient.delete(this.moviesurl + '/' + id);
  }

  // 更新电影
  updateMovie(movie: Movie): Observable<Movie> {
    return this.httpClient.put<Movie>(this.moviesurl, movie);
  }

  // 搜索电影
  searchMovies(term: string): Observable<Movie[]> {
    if (!term.trim()) {
      // if not search term, return empty hero array.
      return of([]);
    }

    return this.httpClient.get<Movie[]>(`${this.moviesurl}`).pipe(
      tap(x => x.length ? console.log(`found heroes matching "${term}"`) :
        console.log(`no heroes matching "${term}"`)),
      catchError(this.handleError<Movie[]>('searchHeroes', []))
    );
  }
}
